import * as React from 'react';
import { AppBar, Typography, Toolbar, Button, createStyles, WithStyles, Avatar, Fab, Icon } from '@material-ui/core';
import { withStyles } from '@material-ui/styles';
import chef from '../static/chef.svg';
import aboutapp from '../static/aboutapp.svg';
import ReactSVG from 'react-svg'
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import image1 from '../static/image1.png';
import image2 from '../static/image2.png';
import pencil from '../static/pencil.svg';
import foodie from '../static/foodie.png';
import prototype from '../static/prototype.svg';
import wip from '../static/wip.svg';
import finalprototype from '../static/finalprototype.svg';
import blueprint from '../static/blueprint.jpg';
import ArrowUpward from '@material-ui/icons/ArrowUpward';

const images = [
    image1,
    image2,
];

const styles = () => createStyles({
    menuButtons: {
        color: 'white',
        marginRight: '30px'
    },
    svg: {
        width: '75vh',
        float: 'right',
        marginRight: '7%',
    },
    svgContent: {
        width: '300px',
        height: '300px',
        float: 'left',
        marginRight: '7%',
    },
    header: {
        width: '100%',
        height: '62vh',
        background: 'radial-gradient(circle at 50% -300%,#FF8401 90%,transparent 90.1%),radial-gradient(circle at 60% -300%,#ffae57 90%,transparent 90.1%)'
    },
    skewDiv: {
        position: 'relative',
        paddingTop: '60px',
        '&::before': {
            width: '100%',
            height: '180%',
            transform: 'skewY(-8deg)',
            background: '#f6f9fc',
            position: 'absolute',
            content: '""',
            zIndex: -1,
            top: 0
        }
    },
    logoImages: {
        width: '200px',
        height: '200px',
        cursor: 'pointer',
        marginRight: '25px'
    },
    pencilSvg: {
        width: '50px',
        height: '50px'
    },
    prototypeSvg: {
        width: '40px',
        height: '40px',
    },
    wip: {
        width: '20%'
    },
    prototypeBackground: {
        position: 'relative',
        height: '100%',
        marginTop: '90px',
        padding: '50px',
        zIndex: 1,
        backgroundImage: `url(${blueprint})`,
        backgroundPosition: 'center'
    },
    finalPrototype: {
        width: '40px',
        height: '40px',
        marginTop: '5px'
    },
})

export interface State {
    prevScrollpos: number;
    visible: boolean;
    photoIndex: number;
    isOpen: boolean;
    isLogoView: boolean;
    goUp: boolean;
}

type PropsAndStyles = WithStyles<typeof styles>;

export class Navbar extends React.Component<PropsAndStyles, State> {
    constructor(props: PropsAndStyles) {
        super(props)
        this.state = {
            prevScrollpos: window.pageYOffset,
            visible: true,
            photoIndex: 0,
            isOpen: false,
            isLogoView: false,
            goUp: false
        }
    }

    componentDidMount() {
        window.addEventListener("scroll", this.isScrolledIntoView);
        window.addEventListener("scroll", this.showGoUp);
        this.imagesLogo();
    }

    componentWillUnmount() {
        window.removeEventListener("scroll", this.isScrolledIntoView);
        window.removeEventListener("scroll", this.showGoUp);
    }

    imagesLogo = () => {
        const r = require.context('../static/', false, /\.(png|jpe?g|svg)$/);
        console.log(r.keys());
    }
    /*handleScroll = () => {
        const { prevScrollpos } = this.state;

        const currentScrollPos = window.pageYOffset;
        const visible = prevScrollpos > currentScrollPos;

        this.setState({
            prevScrollpos: currentScrollPos,
            visible
        });
    }; */

    openLightbox = (index: number) => (event: React.MouseEvent<HTMLImageElement>) => {
        this.setState({
            isOpen: true,
            photoIndex: index,
            visible: false
        })
    }

    generateThumbnail = () => {
        return images.map((img, i) => <img src={img} className={this.props.classes.logoImages} onClick={this.openLightbox(i)} />)
    }

    showGoUp = () => {
        const el = document.getElementById('logo');
        if (el) {
            const rect = el.getBoundingClientRect();
            const elemTop = rect.top;

            console.log(rect.top, rect.bottom);
            this.setState({
                goUp: rect.bottom < 1400
            });
        }
    }

    isScrolledIntoView = () => {
        const el = document.getElementById('logo');
        let isVisible = false;
        if (el) {
            const rect = el.getBoundingClientRect();
            const elemTop = rect.top;
            const elemBottom = rect.bottom;

            isVisible = elemTop + 80 < window.innerHeight && elemBottom >= 0;

        }
        if (isVisible !== this.state.isLogoView) {
            this.setState({
                isLogoView: isVisible
            });
        }
    }

    jumpTo = (id: string) => (event: React.MouseEvent<HTMLButtonElement>) => {
        const el = document.getElementById(id);

        if (el) {
            el.scrollIntoView({ behavior: 'smooth' });
        }
    }

    render = () => {
        const { classes } = this.props;
        const { photoIndex } = this.state;
        return (

            <div id={'root'}>
                <AppBar style={{ background: 'transparent' }} elevation={0} position={'absolute'}>
                    <div style={{
                        width: '100%', background: 'transparent', borderBottom: '1px solid white', height: '30px', display: 'block',
                        textAlign: 'right'
                    }}>
                        <Typography variant={'caption'} style={{ margin: '5px 10px 5px 0' }}>Interação Pessoa Máquina 19/20</Typography>
                    </div>
                    <Toolbar style={{ padding: '10px' }}>
                        <img src={foodie} />
                        <div style={{ right: '20px', position: 'absolute' }}>
                            <Button className={classes.menuButtons} onClick={this.jumpTo('about')}>Sobre a App</Button>
                            <Button className={classes.menuButtons}>Sobre nós</Button>
                            <Button className={classes.menuButtons} onClick={this.jumpTo('logo')}>Logo</Button>
                            <Button className={classes.menuButtons} onClick={this.jumpTo('firstprototype')}>1º Protótipo</Button>
                            <Button className={classes.menuButtons} onClick={this.jumpTo('finalprototype')}>Protótipo Final</Button>
                        </div>
                    </Toolbar>
                </AppBar>
                <div style={{ height: '20vh', width: '100%', background: '#FF8401', display: 'flex', paddingTop: '50px' }}>
                    <div style={{ flexGrow: 1, marginTop: '15%', marginLeft: '5%' }}>
                        <Typography variant={'h2'} style={{ color: 'white', fontWeight: 'bolder' }}>Cook the best recipes.</Typography>
                        <Typography variant={'h5'} style={{ color: 'white' }}>And meet cool people along the way.</Typography>
                        <Button style={{ backgroundColor: 'white', borderRadius: '25px', marginTop: '30px' }}>Discover now</Button>
                    </div>
                    <ReactSVG src={chef}
                        style={{ flexGrow: 1 }}
                        beforeInjection={svg => {
                            svg.classList.add(classes.svg)
                            svg.setAttribute('style', 'viewbox: 0 125 903.77 657.14')
                        }} />
                </div>
                <div className={classes.header} />
                <div style={{ padding: '100px' }} id={'about'}>
                    <Typography variant={'h4'}>About app</Typography>
                    <div style={{ display: 'flex' }}>
                        <ReactSVG src={aboutapp}
                            style={{ display: 'inline-block', flex: 0.45 }}
                            beforeInjection={svg => {
                                svg.classList.add(classes.svgContent)
                            }} />
                        <div style={{ display: 'inline-block', flex: 1, paddingTop: '35px' }}>{`Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                        Suspendisse mi nisi, rhoncus ac tristique sed, sodales at nisi. Proin nec interdum dolor. 
                        Morbi fringilla porta nisi, quis scelerisque tortor commodo a. Phasellus nec purus a enim placerat fringilla. 
                        Pellentesque habitant morbi tristique senectus et netus et malesuada fames ac turpis egestas. Nulla at turpis massa. 
                        Etiam in aliquet augue, euismod commodo dui. Suspendisse in dui maximus, mattis tortor a, varius nisl.`} </div>
                    </div>
                </div>
                <div className={classes.skewDiv} id={'logo'}>
                    <div style={{ position: 'relative', textAlign: 'center' }}>

                        <Avatar style={{ backgroundColor: '#fcc07e', width: '50px', height: '50px', margin: '0 auto', marginBottom: '15px' }}>
                            {this.state.isLogoView && <ReactSVG src={pencil}
                                beforeInjection={svg => {
                                    svg.classList.add(classes.pencilSvg)
                                }} />}
                        </Avatar>
                        <Typography variant={'h5'}> Logo Evolution</Typography>
                    </div>
                    <div style={{ padding: '25px 100px 0px 100px' }}>
                        <div style={{ marginBottom: '20px' }}>{`Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                        Suspendisse mi nisi, rhoncus ac tristique sed, sodales at nisi. Proin nec interdum dolor. 
                        Morbi fringilla porta nisi, quis scelerisque tortor commodo a. Phasellus nec purus a enim placerat fringilla. 
                        Pellentesque habitant morbi tristique senectus et netus et malesuada fames ac turpis egestas. Nulla at turpis massa. 
                        Etiam in aliquet augue, euismod commodo dui. Suspendisse in dui maximus, mattis tortor a, varius nisl.`}
                        </div>
                        <div style={{ textAlign: 'center' }}>
                            {this.generateThumbnail()}
                        </div>
                    </div>

                    {this.state.isOpen && <Lightbox

                        mainSrc={images[photoIndex]}
                        nextSrc={images[(photoIndex + 1) % images.length]}
                        prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                        onCloseRequest={() => this.setState({ isOpen: false, visible: true })}
                        onMovePrevRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + images.length - 1) % images.length,
                            })
                        }
                        onMoveNextRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + 1) % images.length,
                            })
                        }
                    />}
                </div>
                <div className={classes.prototypeBackground} id={'firstprototype'}>
                    <div style={{ display: 'table' }}>
                        <Avatar style={{ backgroundColor: '#FF8401', width: '60px', height: '60px', marginRight: '20px' }}>
                            <ReactSVG src={prototype}
                                beforeInjection={svg => {
                                    svg.classList.add(classes.prototypeSvg)
                                }} />
                        </Avatar>
                        <Typography variant={'h4'} style={{ display: 'table-cell', verticalAlign: 'middle' }}>
                            First Prototype
                            </Typography>
                    </div>
                    <div style={{ textAlign: 'center' }}>
                        <ReactSVG src={wip}
                            beforeInjection={svg => {
                                svg.classList.add(classes.wip)
                            }} />
                        <Typography>⚠️ Prototypes are currently a Work in Progress. Please check later! ⚠️</Typography>
                    </div>
                </div>
                <div style={{ backgroundColor: '#FAFAFA', zIndex: 1, height: '400px', padding: '50px' }}
                    id={'finalprototype'}>
                    <div style={{ display: 'table' }}>
                        <Avatar style={{ backgroundColor: '#FF8401', width: '60px', height: '60px', marginRight: '20px' }}>
                            <ReactSVG src={finalprototype}
                                beforeInjection={svg => {
                                    svg.classList.add(classes.finalPrototype)
                                }} />
                        </Avatar>
                        <Typography variant={'h4'} style={{ display: 'table-cell', verticalAlign: 'middle' }}>
                            Final Prototype
                            </Typography>
                    </div>
                    <div style={{ textAlign: 'center', marginTop: '20px' }}>
                        <ReactSVG src={wip}
                            beforeInjection={svg => {
                                svg.classList.add(classes.wip)
                            }} />
                        <Typography>⚠️ Prototypes are currently a Work in Progress. Please check later! ⚠️</Typography>
                    </div>
                </div>
                {this.state.goUp && <Fab onClick={this.jumpTo('root')}
                    id={'scrollButton'} style={{ position: 'fixed', bottom: '20px', right: '20px', zIndex: 3 }}>
                    <ArrowUpward />
                </Fab>}
            </div>);
    }
}

export default withStyles(styles)(Navbar);